*{
  font-family: 'Courier New', Courier, monospace;
  background: #403A3E;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #BE5869, #403A3E);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #BE5869, #403A3E); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */

}

body{
  padding: 50px;
}

input {
  color: green;
  font-size: 1.2rem;
  width: 100%;
}

#title {
  color:black;
  text-align: center;
  size: 50px;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card {
  align-content:inherit;
  border: 2px solid grey;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}

.card p {
  padding: 5px;
  text-align: center;
}

.card img {
  max-height: 200px;
  width: 100%;
}

.animate__bounceIn {
  --animate-duration: 1.5s;
}

.animate__flip {
  --animate-duration: 1s;
}

.animate__fadeIn {
  --animate-duration: 2s;
}